*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.messageItem{
  background: rgb(100, 255, 227);
  margin-top: 1.5em;
  margin-left: 3em;
  border-radius: 10px;
  padding: 0.5em;
  width: 40%;
  position: relative;
  
}
.message{
  padding-left: 1em;
  font-weight: 600;
}
.messageItem[data-itsme="false"]{
  margin-left: 50%;
  background-color: rgb(196, 196, 196);
}
.messageItem img{
  width: 25px;
  height: 25px;
  border-radius: 20px;
  position: absolute;
  left: -10%;
  top: 0;
  bottom: 0;
}
